html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
  margin: 0;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  margin-bottom: 0;
}

.contenedor2 {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Source Sans Pro", sans-serif;
  margin: 0 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.backgris {
  background-color: #e1e1e1;
}

.no-margin {
  margin-top: 0;
  margin-bottom: 0;
}

.background-azul {
  background: linear-gradient(to bottom, #02f2ee, #212179);
  background-size: cover;
}

.background-azul2 {
  background: linear-gradient(to bottom, #02f2ee, #212179);
  background-size: cover;
}

.background-negro-gris {
  background: linear-gradient(to bottom, #000000, #e1e1e1);
  background-size: cover;
}

.mensaje {
  text-transform: uppercase;
  font-weight: 700;
}
@media (max-width: 500px) {
  .mensaje {
    font-size: 1rem;
  }
}
.mensaje.error {
  background-color: rgba(255, 0, 0, 0.8);
}

.boton1 {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  margin-top: 2.5rem;
  border: none;
}
.boton1:hover {
  background-color: #c77808;
  cursor: pointer;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  background: #12C987;
}

.formulario-estudiante form {
  background: #FFFFFF;
  border-radius: 2rem;
  box-shadow: #bbbbbb 0 0px 20px -1px;
  display: flex;
  flex-direction: column;
}
.formulario-estudiante h1 {
  font-size: 4rem;
  font-weight: 700;
}
.formulario-estudiante fieldset {
  border: 0px;
  display: flex;
  flex-direction: column;
}
.formulario-estudiante label {
  text-align: center;
  font-size: 2.5rem;
}
.formulario-estudiante input {
  height: 4rem;
  background-color: #e1e1e1;
}
.formulario-estudiante input[type=submit] {
  width: 100%;
}
.formulario-estudiante input[type=submit]:hover {
  background-color: #71B100;
}

.formulario-compra fieldset {
  border: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario-compra input {
  border: 0px;
  width: 100%;
  border-radius: 0.5rem;
}
.formulario-compra .formulario-targeta {
  display: flex;
  justify-content: space-between;
  height: 5rem;
  padding-top: 1.5rem;
  padding-bottom: 1rem;
}
@media (max-width: 480px) {
  .formulario-compra .formulario-targeta {
    width: 20rem;
    padding-top: 0.2rem;
    padding-bottom: 0.1rem;
  }
}
@media (max-width: 480px) {
  .formulario-compra .formulario-targeta input {
    width: 9rem;
  }
}
.formulario-compra .formulario-targeta input:first-of-type {
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0rem;
  border-top-right-radius: 0rem;
}
.formulario-compra .formulario-targeta input:last-of-type {
  border-top-left-radius: 0rem;
  border-bottom-left-radius: 0rem;
  border-bottom-right-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}
.formulario-compra label {
  border: 0px;
  font-size: 2rem;
}
.formulario-compra input[type=submit] {
  margin-top: 2rem;
  width: 100%;
}
.formulario-compra input[type=submit]:hover {
  background-color: #71B100;
}

.header {
  background-image: linear-gradient(rgba(0, 65, 64, 0.6), rgba(0, 46, 45, 0.6)), url(../img/trading1.webp);
  background-size: cover;
