  }
}

.text {
  display: block;
  color: rgba(201, 252, 16, 0.9);
  font-size: 5rem;
  line-height: 1.2;
  font-family: monospace;
  font-weight: bold;
  background-image: url("data:image/svg+xml,<svg width='150px' height='150px' xmlns='http://www.w3.org/2000/svg'> <clipPath id='emojiClipPath'> <text x='0' y='130px'  font-size='130px'>ðŸ‘»</text> </clipPath> <text x='0' y='130px' font-size='130px' clip-path='url(%23emojiClipPath)'>ðŸ‘»</text></svg>");
  background-repeat: no-repeat;
  background-size: contain;
  animation: bg 8s infinite alternate;
}
@media (max-width: 500px) {
  .text {
    font-size: 3.5rem;
    margin-left: 2rem;
    background-image: url("data:image/svg+xml,<svg width='70px' height='70px' xmlns='http://www.w3.org/2000/svg'> <clipPath id='emojiClipPath'> <text x='0' y='130px'  font-size='130px'>ðŸ‘»</text> </clipPath> <text x='0' y='130px' font-size='130px' clip-path='url(%23emojiClipPath)'>ðŸ‘»</text></svg>");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

@keyframes bg {
  0% {
    background-position-x: 0%;
  }
  100% {
    background-position-x: 100%;
  }
}
:checked ~ .full-page .text {
  -webkit-background-clip: text;
  background-clip: text;
  color: rgba(0, 0, 0, 0.3);
}

