.accordion {
  width: 100%;
  max-width: 500px;
  overflow: hidden;
  background-color: #e1e1e1;
}
.accordion__intro {
  text-align: center;
  padding: 3px;
  cursor: pointer;
}
.accordion__content {
  padding: 0 20px;
  max-height: 0;
  overflow: hidden;
  will-change: max-height;
  transition: all 0.25s ease-out;
  color: #f1f1f1;
  opacity: 0;
}
.accordion h4,
.accordion p {
  margin: 0;
  line-height: 1;
}
.accordion h4 {
  color: #212179;
  font-size: 26px;
  font-weight: 700;
}
.accordion p {
  color: #8f17e4;
  font-size: 15px;
  margin: 0;
}
.accordion__active .accordion__intro {
  text-align: center;
}
.accordion__active .accordion__intro h4 {
  font-size: 1.2rem;
}
.accordion__active .accordion__content {
  opacity: 1;
}
.accordion__active .accordion__content p {
  font-size: 2rem;
}

.accordion-compra {
  max-width: 500px;
  background: linear-gradient(to right, #28c56f, #00c500);
  will-change: margin-bottom;
  transition: all 0.3s ease-out;
  height: 5rem;
  border-radius: 2rem;
}
.accordion-compra-intro {
  position: relative;
  padding: 5px;
  cursor: pointer;
}
.accordion-compra-content {
  padding: 0 20px;
  color: #f1f1f1;
  opacity: 0;
  will-change: opacity;
  transition: all 0.6s ease-out;
}
.accordion-compra h3,
.accordion-compra p {
  margin: 0;
  line-height: 1;
  color: #FFFFFF;
}
.accordion-compra h3 {
  color: #fff;
  font-size: 26px;
  width: 100%;
}
