.seccion2 {

    background-position: center center;
    background-size: cover;
    padding: $separacion 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5rem;
    h1 {
        font-size: 3rem;
        color: $blanco;
        font-weight: $black;
        text-shadow: 1px 1px 2px $gris, 0 0 1em blue, 0 0 0.2em blue;
    }
    p {
        font-size: 1.7rem;
        color: $blanco;
        padding: 0 1rem;
    }
    a {
        text-align: center;
    }
}