.seccion1 {
    height: 150rem;
    @include tablet {
        @include grid(3, 4rem);
    }
    @include desktop {
        height: 70rem;
    }


}
.icono {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    will-change: font-size;
    transition: all 0.5s ease-out;
    @include desktop {
        height: 55rem;
    }
    &:hover {
        box-shadow: 3px 3px 3px 2px rgba(0, 0, 0, 0.6);
        p {
            font-size: 2.4rem;
        }
    }
    img{
        height: 12rem;
        width: 12rem;
    }
    h3 {
        text-transform: uppercase;
    }
    p {
        padding-bottom: 2rem;
    }
}