
.seccion3 {
    display: grid;
    grid-template-columns: 1fr;
    @include tablet {
        gap: 2rem;
        grid-template-columns: 1fr 3fr;
        height: 38rem;
        padding-top: 0;
    }
    padding-top: 2rem;
    height: 73rem;
}
.contenido-metodologia {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @include tablet {
        flex-direction: row;
    }
    img {
        height: 14rem;
        width: 20rem;
        @include tablet {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }
    h1 {
        color: $blanco;
        font-weight: $bold;
    }
    h3 {
        color: $blanco;
    }
    p {
        color: $blanco;
        margin-bottom: 1.5rem;
    }
}
.imagen-reloj {
    @include desktop {
        height: 60rem;
        width: 180rem;
        padding-bottom: 10rem;
        img {
            height: 100%;
            width: 100%;
            box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.5);
        }
    }
}