.contenido-curso {
    @include tablet {
        display: grid;
        grid-template-columns: 2fr 1fr;
    }
    h2 {
        color: $blanco;
        font-weight: $bold;
    }
}





.lista1 {
    h2{
        color: black;
    }
    background-color: $blanco;
    border-radius: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-right: 1rem;
    margin-right: 2rem;

    opacity: 1;
    will-change: opacity;
    transition: all 0.60s ease-out;
}

.lista2 {
    &-ocultar {
        opacity: 0;
        height: 0;
    }
}
.lista2-curso {
    height: 40rem;
        padding-left: 0rem;
    @include tablet {
        height: 80rem;
        width: 50rem;
        padding: 0;
    }
    @include desktop {
        height: 60rem;
    }

    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $gris;
    h4 {
        padding: 0;
        margin: 0;
    }
    border: 8px solid rgba(0, 0, 0, 0.5);
}
.li-lista2 {
    @include tablet {
        display: flex;
    justify-content: space-between;
    border-bottom:solid 1px $negro;
  width:100%;
  height: 5.3rem;
  &:last-of-type {
    justify-content: space-between;
    border-bottom:solid 0px grey;
  }
  }
  display: block;
  border-bottom:solid 1px grey;
  &:last-of-type {
    justify-content: space-between;
    border-bottom:solid 0px grey;
  }
  margin-bottom: 0;
  height: 4rem;
}
.descripcion-curso {
    margin-right: 2rem;
    h1 {
        color: $blanco;
        font-weight: $black;
    }
    p {
        color: $blanco;
    }
    &-ocultar {
        display: none;
    }
}
.informacion-curso {
    padding-bottom: 3rem;
    @include tablet {
        height: 100rem;
    }

}

.comprar {
    @include tablet {
        margin-left: 3rem;
    }
}
.comprar-contenido {
    margin-top: 1.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background: linear-gradient(to right, rgb(244, 252, 95), rgb(167, 243, 80));
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    border: 6px solid rgba(0,0,0,.5);
    @media (max-width: 480px) {
        max-height: 90rem;
    }
    li {
        display: flex;
        align-items: center;
        img {
            width: 3rem;
            height: 3rem;
            margin-right: 1rem;
        }
    }
    // &.fijo {
    //     position: fixed;
    //     box-shadow:0 .3rem .2rem;
    //     width: 20%;
    // }
    legend {
        background-color:rgb(54, 79, 54);
        border-radius: 1rem;
        padding: .5rem;
    }

}
.precio {
    color: $verde;
    font-weight: 1000;
    strike {
        color: $rojo;
        font-size: 3.5rem;
        margin-right: 3rem;
        font-weight: $regular;
    }
}
.precio-rebajado {
    text-shadow: rgb(34, 174, 6) -1px 1px, rgb(39, 216, 23) -2px 2px, rgb(0, 255, 21) -3px 3px, rgb(51, 255, 102) -4px 4px;
    letter-spacing: 1px;
}
#bt-up{
position:absolute;
z-index:1;
margin: 2rem, 2rem;
margin-right: 4rem;
width: 20rem;
p{
    text-align: center;
    background-color:rgb(244, 252, 95);
}
:hover {
    cursor: pointer;
}
}
#bt-down{
    margin: 2rem, 2rem;
    margin-right: 4rem;
    width: 20rem;
    p{
        text-align: center;
        background-color:rgb(244, 252, 95);
    }
    :hover {
        cursor: pointer;
    }
}
.ocultar {
    display: none;
    opacity: 0;
}

//Accordion
.accordion {
    width: 100%;
    max-width: 500px;
    overflow: hidden;
    background-color: $gris;

    &__intro {
        text-align: center;
		padding: 3px;
		cursor: pointer;
    }
    &__content {
        padding: 0 20px;
		max-height: 0;
		overflow: hidden;
		will-change: max-height;
		transition: all 0.25s ease-out;
		color: #f1f1f1;
		opacity: 0;
    }
    h4,
	p {
		margin: 0;
		line-height: 1;
	}

	h4 {
		color: rgb(33, 33, 121);
		font-size: 26px;
		font-weight: $bold;
	}

	p {
		color: $morado;
		font-size: 15px;
		margin: 0;
	}

	&__active {
		.accordion__intro {
            text-align: center;
            h4 {
                font-size: 1.2rem;
            }
		}

		.accordion__content {
			opacity: 1;
            p {
                font-size: 2rem;
            }
		}
	}
}
//accordion compra   
.accordion-compra {
    max-width: 500px;
    background: linear-gradient(to right, rgb(40, 197, 111), rgb(0 , 197, 0));
    will-change: margin-bottom;
	transition: all 0.30s ease-out;
    height: 5rem;
    border-radius: 2rem;

    &-intro {
        position: relative;
		padding: 5px;
		cursor: pointer;
    }
    &-content {
        padding: 0 20px;
		color: #f1f1f1;
        opacity: 0;
        will-change: opacity;
        transition: all 0.60s ease-out;
    }
    h3,
	p {
		margin: 0;
		line-height: 1;
        color: $blanco;
	}
    h3 {
        color: #fff;
        font-size: 26px;
        width: 100%;
    }


	p {
		color: $morado;
		font-size: 15px;
		margin: 0;
	}
    input {
        height: 3rem;
    }
    .formulario-targeta{
        input {
            width: 70%;
        }
    }
    label {
        text-align: center;
        font-weight: $bold;
        color: $negro;
    }

	&-active {
            overflow: visible;
            .accordion-compra-content {
                opacity: 1;
            }
            height: 33rem;
        }
	
}