.header {
    background-image: 
    linear-gradient(
        rgba(0, 65, 64, 0.6),
        rgba(0, 46, 45, 0.6)
    ),
    url(../img/trading1.webp);
    background-size: cover;
    background-position: center center;
    @include tablet {
        height: 75rem;
        padding-top: 1rem;
        width: auto;
    }
    height: 90rem;
    padding-top: 1rem;
    width: auto;


    h2 {
        padding-top: 10rem;
        color: $blanco;
        font-weight: $bold;
    }
    
}
.logotipo {
    color: $blanco;

    h1 {
        font-size: 3.5rem;
        font-weight: $black;
    }
    span {
        font-size: 5.5rem;
        color: darken($blanco, 10);
        font-weight: $bold;
    }
    &:hover {
        h1 {
            font-size: 4rem;
        }
        span {
            font-size: 6rem;
        }
    }
    @include tablet {
        h1 {
            font-size: 5rem;
            font-weight: $black;
        }
        span {
            font-size: 7rem;
            color: darken($blanco, 10);
            font-weight: $bold;
        }
        &:hover {
            h1 {
                font-size: 5.5rem;
            }
            span {
                font-size: 8rem;
            }
        }
    }
}
.barra {
    @include tablet {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    display: flex;
    flex-direction: column;
    .navegacion {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @include tablet {
            flex-direction: row;
            max-height: 40rem;
        }
        a {
            color: $blanco;
            font-weight: $bold;
            font-size: 2.5rem;
            margin-right: 2rem;


            &:hover {
                color: $amarillo;
                font-size: 2.7rem;
            }
        }
    }
}
.header-normal {
    background:  rgb(2, 242, 238);
    background-size: cover;
    background-position: center center;
    @include tablet {
        height: 17rem;
        padding-top: 1rem;
        width: auto;
    }
    height: 30rem;
    padding-top: 1rem;
    width: auto;
        .logotipo {
            h1 {
                color: $grisOscuro;
            }
            span {
                color: $negro;
            }
        }

    
}


//Text with animated emoji
.full-page{
    display:flex; 
    height:20rem;
    @media(max-width: 500px){
        height: 10rem;
        padding-bottom: 10rem;
    }
    padding-top: 6rem;

  }
  
  .text{
    display:block;
    color: rgba(201, 252, 16, 0.9);
    font-size:5rem;
    line-height:1.2;
    font-family:monospace; 
    font-weight:bold;
    @media(max-width: 500px){
        font-size: 3.5rem;
        margin-left: 2rem;
        background-image: url("data:image/svg+xml,<svg width='70px' height='70px' xmlns='http://www.w3.org/2000/svg'> <clipPath id='emojiClipPath'> <text x='0' y='130px'  font-size='130px'>ðŸ‘»</text> </clipPath> <text x='0' y='130px' font-size='130px' clip-path='url(%23emojiClipPath)'>ðŸ‘»</text></svg>");
    
        background-repeat:no-repeat;
        background-size:contain;
    }
    
    //background-image: linear-gradient(var(--deg), #e66465, #9198e5);
      background-image: url("data:image/svg+xml,<svg width='150px' height='150px' xmlns='http://www.w3.org/2000/svg'> <clipPath id='emojiClipPath'> <text x='0' y='130px'  font-size='130px'>ðŸ‘»</text> </clipPath> <text x='0' y='130px' font-size='130px' clip-path='url(%23emojiClipPath)'>ðŸ‘»</text></svg>");
    
    background-repeat:no-repeat;
    background-size:contain;
    
    animation: bg 8s infinite alternate;
  }
  
  
  @keyframes bg{
    0%{background-position-x:-0%;}
    100%{background-position-x:100%;}
  }
  
  
  
  
  
  :checked ~.full-page .text{
    -webkit-background-clip: text;
    background-clip: text;
    color:rgb(0,0,0,0.3);
  }
