.nosotros {  
    padding: 2rem;
    .contenido-nosotros {
        @include tablet {
            display: grid;
            grid-template-columns: 3fr 3fr;
            gap: 2rem;
        }
    }
    .texto-nosotros {
        background-color: $blanco;
        border-radius: 2rem;

        a {
            margin : 0;
            display: block;
            border-radius: 2rem;
        }
    }
    img {
        border-radius: 2rem;
        margin-bottom: 2rem;
    }
    @include tablet {
        h1, h2, p {
            padding: 1rem;
        }
    }
    h1, h2, p {
        padding-left: 1rem;
        padding-right: 1rem;
    }
    h1 {
        color: $negro;
        font-weight: $bold;
    }
    h2 {
        color: $amarillo;
        font-weight: $black;
    }
    p {
        color: $negro;
        font-weight: $regular;

        &:last-of-type{
            color: $negro;
            margin-bottom: 3rem;
        }
    }
}
