.footer {
    background-color: rgb(33, 33, 121);
    padding-top: 1rem;
    .navegacion {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        @include tablet {
            justify-content: center;
            flex-direction: row;
        }
    }
    a {

        padding: 0.5rem;
        font-size: 1.5rem;
        align-items: center;
        color: $verde;
        font-weight: $black;
        @include tablet {
            padding: 2rem;
            font-size: 2rem;
        }

        &:hover {
            background-color: $negro;
            
        }
    }


    p{
        text-align: center;
    }
}